* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  background-color: var(--primary-background-color);
  min-height: 100vh;
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}
header {
  width: 100%;
  padding: 1rem;
}
.loading,
.errorMessage {
  font-size: 1.5rem;
  font-weight: 500;
  color: var(--primary-font-color);
}
main {
  display: flex;
  gap: 1rem;
  padding: 1rem;
  animation: fade-in 1s ease-in-out;
}
main > aside {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
main > div {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

/* Animations */

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Media query */

@media (max-width: 725px) {
  header {
    padding: calc(1rem * 1.5);
  }
  main {
    flex-direction: column;
    gap: calc(1rem * 1.5);
    padding: calc(1rem * 1.5);
  }
  main > aside {
    gap: calc(1rem * 1.5);
  }
  main > div {
    gap: calc(1rem * 1.5);
  }
}

